import { Globals } from '../../globals';
import { cardTemplate } from '../../card/card.template';
import { Card } from '../../card/card.model';

export class AddCardFormComponent {
  showForm = (index: number, event: Event): boolean => {
    event.preventDefault();
    event.stopPropagation();

    let boardNode = Globals.boardNode.querySelector(`#list-${index}`)!;
    let formNode = boardNode.querySelector('.form-add-card')! as HTMLElement;
    let titleNode = formNode.querySelector(
      '.inp-card-title'
    )! as HTMLInputElement;

    titleNode.focus();
    formNode.style.display = 'block';

    return false;
  };

  hideForm = (index: number, event: Event): boolean => {
    event.preventDefault();

    let boardNode = Globals.boardNode.querySelector(`#list-${index}`)!;
    let formNode = boardNode.querySelector('.form-add-card')! as HTMLElement;

    formNode.style.display = 'none';

    return false;
  };

  addCardOnEnter = (index: number, event: KeyboardEvent): boolean => {
    if (event.keyCode === 13) {
      this.addCard(index, event);
    }

    return false;
  };

  addCard = (index: number, event: Event): boolean => {
    event.preventDefault();

    let boardNode = Globals.boardNode.querySelector(`#list-${index}`)!;
    let formNode = boardNode.querySelector('.form-add-card')! as HTMLElement;
    let titleNode = formNode.querySelector(
      '.inp-card-title'
    )! as HTMLInputElement;
    let boardListNone = boardNode.querySelector('.list-cards')! as HTMLElement;

    let card = new Card(titleNode.value, index);

    boardListNone.insertAdjacentHTML('beforeend', cardTemplate(card));

    titleNode.value = '';
    this.hideForm(index, event);

    return false;
  };
}



