    board1.cards.push(
      new Card(
        'Os cartões fazem muitas coisas legais. Clique neste cartão para abrir e saber mais...',
        0
      )
    );
    board1.cards.push(
      new Card(
        'Adicione membros a um quadro (via barra lateral) para colaborar, compartilhar e discutir. ',
        0
      )
    );

    let board2 = new List('Quadro 2', 1);
    board2.cards.push(
      new Card(
        'Também 2 é possível verificar a atividade do quadro, alterar a tela de fundo e muito mais.',
        0
      )
    );
    board2.cards.push(
      new Card(
        'Os 3 cartões fazem muitas coisas legais. Clique neste cartão para abrir e saber mais...',
        0
      )
    );
    board2.cards.push(
      new Card(
        'Adicione 4 membros a um quadro (via barra lateral) para colaborar, compartilhar e discutir. ',
        0
      )
    );

    board.lists.push(board1);
    board.lists.push(board2);
    board.lists.push(board2);
    board.lists.push(board1);

    boardTemplate(board.lists);
    Globals.boardNode.insertAdjacentHTML(
      'beforeend',
      boardTemplate(board.lists)
    );
