import { List } from '../list/list.model';
import { board } from '../app';
import { cardTemplate } from '../card/card.template';
import { listHeaderTemplate } from './list-header/list-header.template';
import { addCardFormTemplate } from './add-card-form/add-card-form.template';

export const listTemplate = (list: List) => {
  return `
            <div 
                class="list-wrapper"
                ondragover="App.list.onDragOver(event)"
                ondragend="App.list.onDragEnd(event)"                
            >
                <div 
                    class="list" id="list-${list.index}" 
                    data-list-index="${list.index}"
                    ondragstart="App.list.onDragStart(event)" 
                    draggable="true"                    
                >   
                    <div class="list-content">
                       ${listHeaderTemplate(list.title)}
                        <div class="list-cards">
                            ${list.cards
                              .map(card => `${cardTemplate(card)}`)
                              .join('')}
                        </div>
                        ${addCardFormTemplate(list.index)}
                    </div>
                </div>
            </div>
        `;
};



