import { Globals } from '../globals';
import { Util } from '../util';

export class ListComponent {
  onDragStart = (ev: Event) => {
    let event = ev as DragEvent;

    const target = ev.target as HTMLElement;

    Globals.draggableElement = target;

    event.dataTransfer.setData('text/html', target.innerHTML);
    event.dataTransfer.effectAllowed = 'move';
  };

  onDragOver = (ev: Event) => {
    let event = ev as DragEvent;

    event.preventDefault();
    event.dataTransfer.dropEffect = 'move';

    const target = ev.target as HTMLElement;

    if (!Util.hasClass(Globals.draggableElement, 'card')) {
      document.querySelectorAll('.list').forEach(element => {
        let e = element as HTMLElement;

        e.style.zIndex = '-1';
      });
    }

    if (Util.hasClass(Globals.draggableElement, 'list')) {
      if (!target.classList.contains('list-wrapper')) {
        return;
      }

      const parent = target.parentElement! as HTMLElement;
      const next = target.nextElementSibling! as HTMLElement;
      const previous = target.previousElementSibling! as HTMLElement;
      const grandpa = parent.parentElement! as HTMLElement;
      const first = parent.querySelector('.list-wrapper')! as HTMLElement;
      const last = parent.lastElementChild! as HTMLElement;

      if (Util.hasClass(parent, 'board')) {
        Globals.draggableElement.style.background = 'rgba(0, 0, 0, .2)';
        let listContentNode = Globals.draggableElement.querySelector(
          '.list-content'
        )! as HTMLElement;

        listContentNode.style.visibility = 'hidden';

        if (target && target !== Globals.draggableElement.parentElement) {
          if (first === Globals.draggableElement.parentElement!) {
            parent.insertBefore(
              target,
              Globals.draggableElement.parentElement!
            );
          } else if (first === target) {
            parent.insertBefore(
              Globals.draggableElement.parentElement!,
              target
            );
          } else if (last === Globals.draggableElement.parentElement!) {
            parent.insertBefore(
              Globals.draggableElement.parentElement!,
              target
            );
          } else {
            if (Globals.draggableElement.parentElement! == next) {
              parent.insertBefore(
                Globals.draggableElement.parentElement!,
                target
              );
            } else {
              parent.insertBefore(
                Globals.draggableElement.parentElement!,
                next
              );
            }
          }
        }
      }
    }

    if (Util.hasClass(Globals.draggableElement, 'card')) {
      if (!target.getElementsByClassName('list-cards').length) {
        return;
      }

      Globals.draggableElement.style.background = '#C1C8CB';
      let cardConten = Globals.draggableElement.getElementsByClassName(
        'card-content'
      )[0] as HTMLElement;
      cardConten.style.visibility = 'hidden';

      target
        .getElementsByClassName('list-cards')[0]
        .insertAdjacentElement('beforeend', Globals.draggableElement);
    }
  };

  onDragEnd = (ev: Event) => {
    ev.preventDefault();

    let element = ev.target as HTMLElement;

    document.querySelectorAll('.list').forEach(element => {
      let e = element as HTMLElement;

      e.style.zIndex = '10';
    });

    if (Util.hasClass(Globals.draggableElement, 'list')) {
      Globals.draggableElement.style.background = '#E1E3E5';
      let listContentNode = Globals.draggableElement.querySelector(
        '.list-content'
      )! as HTMLElement;

      listContentNode.style.visibility = 'visible';
    }
  };
}



