import { Card } from '../card/card.model';

export class List {
  public title: string;
  public index: number;
  public cards: Card[];
  public element: HTMLElement;

  constructor(title: string, index: number) {
    this.title = title;
    this.index = index;
    this.cards = [];
  }
}



